"use client"

import type React from "react"

import { useState, useEffect, useCallback } from "react"
import { useSearchParams } from "next/navigation"
import { Card, CardHeader, CardContent, CardTitle, CardDescription } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { FileUp, Loader, FileText, FileIcon as FilePresentation, Download, RefreshCw } from "lucide-react"
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert"
import { Label } from "@/components/ui/label"
import { Separator } from "@/components/ui/separator"
import { Badge } from "@/components/ui/badge"

// Define the interface for document objects
interface SubmittedDocument {
  idPFE: number
  idLivrable: 1 | 2 // Limit to the values we know exist
  fichier: string
}

// Define the interface for livrable names mapping
interface LivrableNames {
  [key: number]: string
}

export default function SoumettrePage() {
  const searchParams = useSearchParams()
  const idEtudiant = searchParams.get("idEtudiant") || "4" // Default for testing
  const idGroupe = searchParams.get("idGroupe") || "2" // Get from URL or default

  // State for form files
  const [pdfFile, setPdfFile] = useState<File | null>(null)
  const [presentationFile, setPresentationFile] = useState<File | null>(null)

  // Form submission state
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [submitSuccess, setSubmitSuccess] = useState(false)
  const [submitError, setSubmitError] = useState<string | null>(null)

  // State for submitted documents with proper typing
  const [submittedDocuments, setSubmittedDocuments] = useState<SubmittedDocument[]>([])
  const [isLoading, setIsLoading] = useState(true)
  const [fetchError, setFetchError] = useState<string | null>(null)

  // Map livrable IDs to human-readable names with proper typing
  const livrableNames: LivrableNames = {
    1: "Document PDF",
    2: "Présentation",
  }

  // Map livrable IDs to icons
  const livrableIcons = {
    1: <FileText className="h-4 w-4" />,
    2: <FilePresentation className="h-4 w-4" />,
  }

  // Fetch submitted documents function
  const fetchDocuments = useCallback(async () => {
    setIsLoading(true)
    setFetchError(null)
    try {
      const response = await fetch(`http://localhost:5000/api/livrable/documents?idEtudiant=${idEtudiant}`)
      if (response.ok) {
        const data = (await response.json()) as SubmittedDocument[]
        setSubmittedDocuments(data)
      } else {
        setFetchError("Impossible de récupérer les documents")
      }
    } catch (error) {
      setFetchError("Erreur lors de la récupération des documents")
      console.error("Fetch error:", error)
    } finally {
      setIsLoading(false)
    }
  }, [idEtudiant])

  // Fetch submitted documents when component mounts
  useEffect(() => {
    fetchDocuments()
  }, [fetchDocuments])

  // Handle file selection
  const handlePdfChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files?.[0]) setPdfFile(e.target.files[0])
  }

  const handlePresentationChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files?.[0]) setPresentationFile(e.target.files[0])
  }

  // Reset form
  const resetForm = () => {
    setPdfFile(null)
    setPresentationFile(null)
    // Reset file input elements
    const pdfInput = document.getElementById("pdf") as HTMLInputElement
    const presentationInput = document.getElementById("presentation") as HTMLInputElement
    if (pdfInput) pdfInput.value = ""
    if (presentationInput) presentationInput.value = ""
  }

  // Handle form submission
  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault()
    setSubmitSuccess(false)
    setSubmitError(null)
    setIsSubmitting(true)

    if (!pdfFile || !presentationFile) {
      setSubmitError("Veuillez sélectionner les deux fichiers.")
      setIsSubmitting(false)
      return
    }

    const formData = new FormData()
    formData.append("files", pdfFile)
    formData.append("files", presentationFile)
    formData.append("idEtudiant", idEtudiant)
    formData.append("idGroupe", idGroupe)
    formData.append("livrables", JSON.stringify([1, 2])) // 1 for PDF, 2 for presentation

    try {
      const response = await fetch("http://localhost:5000/api/livrable/submit-documents", {
        method: "POST",
        body: formData,
      })

      if (response.ok) {
        setSubmitSuccess(true)
        resetForm()
        fetchDocuments() // Refresh document list after submission
      } else {
        const errorText = await response.text()
        setSubmitError(`Erreur lors de la soumission: ${errorText}`)
      }
    } catch (error) {
      console.error("Erreur de connexion:", error)
      setSubmitError("Erreur de connexion au serveur")
    } finally {
      setIsSubmitting(false)
    }
  }

  return (
    <div className="container mx-auto py-8 px-4">
      <Card className="w-full max-w-2xl mx-auto shadow-lg border-0 overflow-hidden">
        <CardHeader className="bg-gradient-to-r from-primary/10 to-primary/5 pb-6">
          <CardTitle className="text-2xl font-bold">Soumettre des documents</CardTitle>
          <CardDescription>Soumettez vos documents de projet pour évaluation</CardDescription>
        </CardHeader>
        <CardContent className="p-6 space-y-6">
          {/* Feedback alerts */}
          {submitSuccess && (
            <Alert className="bg-green-50 text-green-800 border-green-200">
              <AlertTitle className="font-medium">Succès</AlertTitle>
              <AlertDescription>Documents soumis avec succès!</AlertDescription>
            </Alert>
          )}

          {submitError && (
            <Alert className="bg-destructive/10 text-destructive border-destructive/20">
              <AlertTitle className="font-medium">Erreur</AlertTitle>
              <AlertDescription>{submitError}</AlertDescription>
            </Alert>
          )}

          {/* Submitted Documents Section */}
          <div>
            <div className="flex items-center justify-between mb-4">
              <h2 className="text-lg font-semibold">Documents Soumis</h2>
              <Button variant="outline" size="sm" onClick={fetchDocuments} disabled={isLoading} className="h-8">
                <RefreshCw className={`h-3.5 w-3.5 mr-1.5 ${isLoading ? "animate-spin" : ""}`} />
                Actualiser
              </Button>
            </div>

            {isLoading ? (
              <div className="flex items-center justify-center p-6 bg-muted/20 rounded-lg">
                <Loader className="h-5 w-5 animate-spin mr-2 text-primary" />
                <p className="text-muted-foreground">Chargement des documents...</p>
              </div>
            ) : fetchError ? (
              <Alert variant="destructive" className="bg-destructive/10 text-destructive border-destructive/20">
                <AlertDescription>{fetchError}</AlertDescription>
              </Alert>
            ) : submittedDocuments.length > 0 ? (
              <div className="space-y-3 bg-muted/10 rounded-lg p-4">
                {submittedDocuments.map((doc) => (
                  <div
                    key={`${doc.idPFE}-${doc.idLivrable}`}
                    className="flex items-center justify-between p-3 bg-background rounded-md border shadow-sm"
                  >
                    <div className="flex items-center gap-3">
                      {doc.idLivrable in livrableIcons && livrableIcons[doc.idLivrable as keyof typeof livrableIcons]}
                      <span className="font-medium">{livrableNames[doc.idLivrable]}</span>
                      <Badge variant="outline" className="ml-2 text-xs">
                        Soumis
                      </Badge>
                    </div>
                    <a
                      href={`http://localhost:5000${doc.fichier}`}
                      download
                      className="flex items-center text-primary hover:text-primary/80 transition-colors"
                    >
                      <Download className="h-4 w-4 mr-1" />
                      <span className="text-sm">Télécharger</span>
                    </a>
                  </div>
                ))}
              </div>
            ) : (
              <div className="text-center p-6 bg-muted/10 rounded-lg">
                <p className="text-muted-foreground">Aucun document soumis pour le moment.</p>
              </div>
            )}
          </div>

          <Separator />

          {/* Submission Form */}
          <div>
            <h2 className="text-lg font-semibold mb-4">Soumettre de nouveaux documents</h2>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="pdf" className="flex items-center gap-2">
                  <FileText className="h-4 w-4" />
                  Document PDF
                </Label>
                <div className="relative">
                  <input
                    type="file"
                    id="pdf"
                    accept=".pdf"
                    onChange={handlePdfChange}
                    className="block w-full text-sm text-muted-foreground
                      file:mr-4 file:py-2 file:px-4
                      file:rounded-md file:border-0
                      file:text-sm file:font-medium
                      file:bg-primary file:text-primary-foreground
                      hover:file:bg-primary/90 cursor-pointer
                      border rounded-md"
                    required
                  />
                  {pdfFile && (
                    <Badge className="absolute right-3 top-1/2 transform -translate-y-1/2 bg-primary/10 text-primary border-primary/20 px-2">
                      {pdfFile.name.length > 20 ? `${pdfFile.name.substring(0, 20)}...` : pdfFile.name}
                    </Badge>
                  )}
                </div>
                {!pdfFile && submitError && (
                  <p className="text-sm text-destructive mt-1">Veuillez sélectionner un fichier PDF</p>
                )}
              </div>

              <div className="space-y-2">
                <Label htmlFor="presentation" className="flex items-center gap-2">
                  <FilePresentation className="h-4 w-4" />
                  Présentation
                </Label>
                <div className="relative">
                  <input
                    type="file"
                    id="presentation"
                    accept=".ppt,.pptx,.key,.pdf"
                    onChange={handlePresentationChange}
                    className="block w-full text-sm text-muted-foreground
                      file:mr-4 file:py-2 file:px-4
                      file:rounded-md file:border-0
                      file:text-sm file:font-medium
                      file:bg-primary file:text-primary-foreground
                      hover:file:bg-primary/90 cursor-pointer
                      border rounded-md"
                    required
                  />
                  {presentationFile && (
                    <Badge className="absolute right-3 top-1/2 transform -translate-y-1/2 bg-primary/10 text-primary border-primary/20 px-2">
                      {presentationFile.name.length > 20
                        ? `${presentationFile.name.substring(0, 20)}...`
                        : presentationFile.name}
                    </Badge>
                  )}
                </div>
                {!presentationFile && submitError && (
                  <p className="text-sm text-destructive mt-1">Veuillez sélectionner un fichier de présentation</p>
                )}
              </div>

              <Button type="submit" className="w-full mt-6" disabled={isSubmitting} size="lg">
                {isSubmitting ? (
                  <>
                    <Loader className="mr-2 h-5 w-5 animate-spin" />
                    Soumission en cours...
                  </>
                ) : (
                  <>
                    <FileUp className="mr-2 h-5 w-5" />
                    Soumettre les documents
                  </>
                )}
              </Button>
            </form>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}

